import { getAdPositioneById } from '@/api/userApi.js'
export default {
  name: 'AdvertisingPosition',
  props: {
    num: {
      type: Number,
      default: 0
    }
  },
  data() {
    return {
      adSets: []
    }
  },
  mounted() {
    this.getAdPositione()
  },
  methods: {
    // 广告位url跳转
    goPath(item) {
      if (item.type === 1) {
			  uni.navigateTo({
			  	url:'/pages/common/webview/webview?url='+item.url
			  })
		}else if(item.type === 5){
			uni.navigateTo({
				url:'/pages/user/order/detail?shopId='+item.id+'&goodsId='+item.url
			})
		}
    },
    // 获取广告位数据
    getAdPositione() {
      let data = {
        id: 1
      }
      getAdPositioneById(data).then(res => {
        if (res.code === '1000') {
          this.adSets = res.data.adSet
        }
      })
    }
  }
}
